<template>
  <div class="main">
    <div class="hat">
      <h1 @click="openProfile(us.displayName)">
        Добро пожаловать, {{ us.displayName }}
      </h1>
      <user-avatar
        :photo="us.photoURL"
        alt="user avatar"
        class="user-avatar"
        @click="openProfile(us.displayName)"
      />
      <button @click="signOut">Выйти</button>
    </div>
    <chat-window :messages="usersName" :currentUsername="us.displayName" />
    <hr />
    <send-message-form :displayName="us.displayName" />
  </div>
</template>

<script>
import {
  VTextField,
  VProgressCircular,
} from "vuetify/lib/components/index.mjs";
import { ref, onMounted, watch } from "vue";
import router from "@/router";
import {
  getMessages,
  getCurrentUser,
  signOutUser,
  getUserImage,
} from "@/db/db";
import ChatWindow from "@/components/ChatWindow.vue";
import SendMessageForm from "@/components/SendMessageForm.vue";
import UserAvatar from "@/components/UserAvatar.vue";
import { useUserStore } from "@/store/user";

export default {
  name: "ChatPage",
  components: {
    VTextField,
    VProgressCircular,
    ChatWindow,
    SendMessageForm,
    UserAvatar,
  },

  setup() {
    const inputMessage = ref("");
    const us = ref("");
    const allImagesLoaded = ref(false);

    const { usersName } = getMessages();

    const store = useUserStore();
    const { getUserByName, addUser } = store;

    watch(usersName, async () => {
      for (const user of usersName.value) {
        const userInStore = getUserByName(user.displayName);
        try {
          if (!userInStore || !userInStore.photo) {
            const image = await getUserImage(user.displayName);
            addUser({ username: user.displayName, photo: image });
            user.photo = image;
          } else {
            user.photo = userInStore.photo;
          }
        } catch (error) {
          if (!userInStore || !userInStore.photo) {
            addUser({
              username: user.displayName,
              photo:
                "https://i.pinimg.com/736x/cb/45/72/cb4572f19ab7505d552206ed5dfb3739.jpg",
            });
            user.photo =
              "https://i.pinimg.com/736x/cb/45/72/cb4572f19ab7505d552206ed5dfb3739.jpg";
          }
        }
      }
      allImagesLoaded.value = true;
    });

    const signOut = () => {
      signOutUser();
      router.push({
        name: "login",
      });
    };

    const openProfile = (username) => {
      router.push({
        name: "profile",
        params: {
          name: username,
        },
      });
    };

    onMounted(() => {
      getCurrentUser()
        .then((user) => {
          us.value = user;
        })
        .catch(() => {
          router.push({
            name: "login",
          });
        });
    });

    return {
      inputMessage,
      usersName,
      us,
      allImagesLoaded,
      openProfile,
      signOut,
    };
  },
};
</script>

<style scoped>
* {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ea526f;
  height: 100vh;
}

.hat {
  display: flex;
  width: 100%;
  height: 150px;
  margin-bottom: 20px;
}

.hat > button {
  position: absolute;
  top: 15px;
  right: 15px;
  appearance: none;
  border: none;
  outline: none;
  background: none;

  color: #fff;
  font-size: 18px;
  margin-bottom: 10px;
  text-align: right;
}

h1 {
  align-self: flex-end;
  margin-left: 5px;
}

h1:hover,
.user-avatar:hover {
  cursor: pointer;
}

.user-avatar {
  align-self: flex-end;
  margin-left: 15px;
}
</style>
